import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
// @material-ui/core components
import { makeStyles, withStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import WatchLater from "@material-ui/icons/WatchLater";
import Collapse from "@material-ui/core/Collapse";
import { toast, ToastContainer } from "react-toastify";
import VisibilityIcon from "@material-ui/icons/Visibility";

import "react-toastify/dist/ReactToastify.min.css";
import Tooltip from "@material-ui/core/Tooltip";

import SkeletonMatch from "../SkeletonMatch";

// core components

import axios from "axios";
import api from "../../api";
import styles from "./tableStyle.js";
import FullWidthTabs from "../TabsPreGame";

const api2 = axios.create({
  baseURL: "https://feed.lolesports.com/livestats/v1/window",
});

const useStyles = makeStyles(styles);

const CustomTooltip = withStyles((theme) => ({
  tooltip: {
    background: "#0f1519",
    color: "#ddd",
    padding: 10,
    fontSize: 14,
  },
}))(Tooltip);

export default function TablePreGame(props) {
  const classes = useStyles();
  const [odds, setOdds] = useState([]);
  const [open, setOpen] = React.useState(-1);
  const [blue, setBlue] = useState();
  const [red, setRed] = useState();
  const [bStats, setBlueStats] = useState();
  const [rStats, setRedStats] = useState();
  const [bByYearStats, setBlueByYearStats] = useState();
  const [rByYearStats, setRedByYearStats] = useState();

  const getMatch = async (index, gameId, cell) => {
    async function getGame() {
      setBlue();
      setRed();
      setBlueStats();
      setRedStats();
      setBlueByYearStats();
      setRedByYearStats();
      const res2 = await api.get(
        `/getEventDetails?hl=pt-BR&id=${cell?.match?.id}`,
        {
          headers: {
            "x-api-key": "0TvQnueqKa5mxJntVWt0w4LpLfEkrV1Ta8rQBb9Z",
          },
        }
      );
      var blueTeam = res2?.data?.data?.event?.match?.teams[0].id;
      var redTeam = res2?.data?.data?.event?.match?.teams[1].id;
      console.log(blueTeam);
      if (blueTeam && redTeam) {
        let res;
        let res2;
        let res3;
        let res4;
        const data = await axios.get(
          `https://tgt7ghqvcb.execute-api.us-west-2.amazonaws.com/prod/teams/search?search=`,
          {
            headers: {
              "x-api-key": "f561197a-82ea-4e54-acd2-386979018a7a",
            },
          }
        );
        const teams = data.data;

        const blue = await api.get(`/getTeams?hl=pt-BR&id=${blueTeam}`, {
          headers: {
            "x-api-key": "0TvQnueqKa5mxJntVWt0w4LpLfEkrV1Ta8rQBb9Z",
          },
        });
        const red = await api.get(`/getTeams?hl=pt-BR&id=${redTeam}`, {
          headers: {
            "x-api-key": "0TvQnueqKa5mxJntVWt0w4LpLfEkrV1Ta8rQBb9Z",
          },
        });
        setBlue(blue.data.data.teams[0]);
        setRed(red.data.data.teams[0]);

        const blueCode = blue?.data?.data?.teams[0].code;
        const redCode = red?.data?.data?.teams[0].code;
        const blueLeague = blue?.data?.data?.teams[0].homeLeague.name;
        const redLeague = red?.data?.data?.teams[0].homeLeague.name;

        var blueAcademyName = blueCode?.toLowerCase().split("");
        blueAcademyName[blueAcademyName.length - 1] = `.${
          blueAcademyName[blueAcademyName.length - 1]
        }`;
        blueAcademyName = blueAcademyName.join("");
        var redAcademyName = redCode?.toLowerCase().split("");
        redAcademyName[redAcademyName.length - 1] = `.${
          redAcademyName[redAcademyName.length - 1]
        }`;
        redAcademyName = redAcademyName.join("");

        var blueAcademyName2 = `${blueCode?.toLowerCase()}.a`;
        var redAcademyName2 = `${redCode?.toLowerCase()}.a`;
        console.log(
          blueAcademyName,
          redAcademyName,
          blueAcademyName2,
          redAcademyName2
        );

        console.log(blueCode, blueLeague);

        var teamRed = teams.filter(
          (team) =>
            (team.tag?.toLowerCase() === redCode?.toLowerCase() ||
              team.tag?.toLowerCase() === redAcademyName.toLowerCase() ||
              team.tag?.toLowerCase() === redAcademyName2.toLowerCase()) &&
            (team.league?.toLowerCase() === redLeague?.toLowerCase() ||
              team.league?.toLowerCase() ===
                redLeague.split(" ")[0]?.toLowerCase() ||
              team.league === null)
        );
        if (teamRed.length > 1) {
          teamRed = teamRed.filter(
            (item) => item.league?.toLowerCase() === redLeague?.toLowerCase()
          );
        }
        if (teamRed.length > 1) {
          teamRed = teamRed[0];
        }

        if (teamRed.length === 1) {
          res = await axios.get(
            `https://tgt7ghqvcb.execute-api.us-west-2.amazonaws.com/prod/teams/statsBySplit/${teamRed[0].teamId}`,
            {
              headers: {
                "x-api-key": "f561197a-82ea-4e54-acd2-386979018a7a",
              },
            }
          );
          res3 = await axios.get(
            `https://tgt7ghqvcb.execute-api.us-west-2.amazonaws.com/prod/teams/statsByYear/${teamRed[0].teamId}`,
            {
              headers: {
                "x-api-key": "f561197a-82ea-4e54-acd2-386979018a7a",
              },
            }
          );
        }

        var teamBlue = teams.filter(
          (team) =>
            (team.tag?.toLowerCase() === blueCode?.toLowerCase() ||
              team.tag?.toLowerCase() === blueAcademyName.toLowerCase() ||
              team.tag?.toLowerCase() === blueAcademyName2.toLowerCase()) &&
            (team.league?.toLowerCase() === blueLeague?.toLowerCase() ||
              team.league?.toLowerCase() ===
                blueLeague.split(" ")[0]?.toLowerCase() ||
              team.league === null)
        );
        console.log(teamBlue, teamRed);
        if (teamBlue.length > 1) {
          teamBlue = teamBlue.filter(
            (item) => item.league?.toLowerCase() === blueLeague?.toLowerCase()
          );
        }
        if (teamBlue.length > 1) {
          teamBlue = teamBlue[0];
        }

        if (teamBlue.length === 1) {
          res2 = await axios.get(
            `https://tgt7ghqvcb.execute-api.us-west-2.amazonaws.com/prod/teams/statsBySplit/${teamBlue[0].teamId}`,
            {
              headers: {
                "x-api-key": "f561197a-82ea-4e54-acd2-386979018a7a",
              },
            }
          );
          res4 = await axios.get(
            `https://tgt7ghqvcb.execute-api.us-west-2.amazonaws.com/prod/teams/statsByYear/${teamBlue[0].teamId}`,
            {
              headers: {
                "x-api-key": "f561197a-82ea-4e54-acd2-386979018a7a",
              },
            }
          );
        }

        const blueStats = res2?.data[0];
        const redStats = res?.data[0];
        const blueByYearStats = res4?.data[0];
        const redSByYeartats = res3?.data[0];
        setBlueStats(blueStats);
        setRedStats(redStats);
        setBlueByYearStats(blueByYearStats);
        setRedByYearStats(redSByYeartats);
      } else
        toast.error("A partida selecionada está indisponivsadasel no momento");
    }

    if (open !== -1) {
      setOpen(-1);
    } else
      getGame().then((res) => {
        setOpen(index);
      });
  };

  const { tableHead, tableData, tableHeaderColor } = props;

  console.log(tableData);

  return (
    <div className={classes.tableResponsive}>
      <ToastContainer style={{ fontSize: 18 }} />
      <Table className={classes.table}>
        {tableHead !== undefined ? (
          <TableHead className={classes[tableHeaderColor + "TableHeader"]}>
            <TableRow className={classes.tableHeadRow}>
              {tableHead.map((prop, key) => {
                return (
                  <TableCell
                    className={classes.tableCell + " " + classes.tableHeadCell}
                    key={key}
                  >
                    {prop}
                  </TableCell>
                );
              })}
            </TableRow>
          </TableHead>
        ) : null}
        <TableBody>
          {typeof tableData !== "undefined" ? (
            tableData.map((cell, index) => (
              <>
                <TableRow
                  data-value="parent"
                  className={
                    (open === index && "selecteD") || "tableBodyRowHover"
                  }
                >
                  <div
                    onClick={() => getMatch(index, cell.gameId, cell)}
                    className={(open === index && "selectedDiv") || ""}
                  >
                    <TableCell className={classes.tableCellTime}>
                      <div class="EventTime">
                        <div class="time">
                          <span class="hour">
                            {("0" + new Date(cell.startTime).getHours()).slice(
                              -2
                            )}
                          </span>
                          <span class="minute">00</span>
                          <span class="approx">APROX.</span>
                        </div>
                      </div>
                    </TableCell>

                    <TableCell className={classes.tableCellTeams}>
                      <div className="divMatch">
                        <div className="divTeam">
                          <div className="divName">
                            <h1 className="teamName">
                              {cell.match.teams[0].name.length < 20
                                ? cell.match.teams[0].name
                                : cell.match.teams[0].code}
                            </h1>
                          </div>

                          <CustomTooltip
                            title={
                              <React.Fragment>
                                <p color="inherit">Ver Histórico</p>
                              </React.Fragment>
                            }
                          >
                            <img
                              data-value="child"
                              onClick={(e) => {
                                e.stopPropagation();
                                const win = window.open(
                                  `https://lucasquintino.github.io/lol-predict-app-frontend/#/history/${cell.league.name}/${cell.match.teams[0].name}`,
                                  "_blank"
                                );
                                win.focus();
                              }}
                              className="imgTeam"
                              src={cell.match.teams[0].image}
                              alt=""
                            />
                          </CustomTooltip>
                        </div>
                        <div className="vsDiv">
                          <h1 className="vs">vs</h1>
                          <a
                            onClick={(e) => e.stopPropagation()}
                            href={`https://lolesports.com/live/${cell?.league?.slug}`}
                            target="_blank"
                          >
                            <CustomTooltip
                              title={
                                <React.Fragment>
                                  <p color="inherit">Assistir Partida</p>
                                </React.Fragment>
                              }
                            >
                              <VisibilityIcon className="viewIcon" />
                            </CustomTooltip>
                          </a>
                        </div>

                        <div className="divTeam2">
                          <CustomTooltip
                            title={
                              <React.Fragment>
                                <p color="inherit">Ver Histórico</p>
                              </React.Fragment>
                            }
                          >
                            <img
                              onClick={(e) => {
                                e.stopPropagation();
                                const win = window.open(
                                  `https://lucasquintino.github.io/lol-predict-app-frontend/#/history/${cell.league.name}/${cell.match.teams[1].name}`,
                                  "_blank"
                                );
                                win.focus();
                              }}
                              className="imgTeam"
                              src={cell.match.teams[1].image}
                              alt=""
                            />
                          </CustomTooltip>

                          <div className="divName">
                            <h1 className="teamName">
                              {cell.match.teams[1].name.length < 20
                                ? cell.match.teams[1].name
                                : cell.match.teams[1].code}
                            </h1>
                          </div>
                        </div>
                      </div>
                    </TableCell>

                    <TableCell className={classes.tableCellLeague}>
                      <div class="league">
                        <div class="name">{cell.league.name.split(" ")[0]}</div>
                        <div class="strategy">
                          melhor de {cell.match.strategy.count}
                        </div>
                      </div>
                    </TableCell>
                  </div>
                  <Collapse in={open == index} timeout="auto" unmountOnExit>
                    <>
                      <main class="">
                        <main class="Watch">
                          <div class="center-pane">
                            <div class="lower">
                              <div class="overview-pane">
                                <div class="VodsGameSelector active-selection">
                                  <div class="games">
                                    <span class="label">PREDIÇÕES</span>
                                  </div>
                                </div>
                                <div class="VodsGameSelector active-selection tab title stats selected">
                                  <div class="type"></div>
                                  <div
                                    class="type"
                                    style={{ background: "#03719C" }}
                                  >
                                    BLUE
                                  </div>

                                  <div
                                    class="type"
                                    style={{ background: "#EE2737" }}
                                  >
                                    RED
                                  </div>
                                </div>
                                <div className="prediction">
                                  <div className="result">VENCEDOR</div>
                                  <div
                                    className={
                                      2 === 1
                                        ? 2 === 1
                                          ? "team1 predicted"
                                          : "team1"
                                        : 2 === 1
                                        ? "team1 predicted"
                                        : "team1"
                                    }
                                  >
                                    <img
                                      className="imgTeam"
                                      src={blue?.image}
                                      alt=""
                                    />
                                    <h1 className="teamName">{blue?.code}</h1>
                                  </div>
                                  <div
                                    className={
                                      2 === 1
                                        ? 2 === 0
                                          ? "team2 predicted"
                                          : "team2"
                                        : 2 === 0
                                        ? "team2 predicted"
                                        : "team2"
                                    }
                                  >
                                    <img
                                      className="imgTeam"
                                      src={red?.image}
                                      alt=""
                                    />
                                    <h1 className="teamName">{red?.code}</h1>
                                  </div>
                                </div>
                                <div className="prediction">
                                  <div className="result">TORRE</div>
                                  <div className="team1">
                                    <img
                                      className="imgTeam"
                                      src={blue?.image}
                                      alt=""
                                    />
                                    <h1 className="teamName">{blue?.code}</h1>
                                  </div>
                                  <div className="team2">
                                    <img
                                      className="imgTeam"
                                      src={red?.image}
                                      alt=""
                                    />
                                    <h1 className="teamName">{red?.code}</h1>
                                  </div>
                                </div>
                                <div className="prediction">
                                  <div className="result">BARÃO</div>
                                  <div className="team1">
                                    <img
                                      className="imgTeam"
                                      src={blue?.image}
                                      alt=""
                                    />
                                    <h1 className="teamName">{blue?.code}</h1>
                                  </div>
                                  <div className="team2">
                                    <img
                                      className="imgTeam"
                                      src={red?.image}
                                      alt=""
                                    />
                                    <h1 className="teamName">{red?.code}</h1>
                                  </div>
                                </div>
                                <div className="prediction">
                                  <div className="result">DRAGÃO</div>
                                  <div className="team1">
                                    <img
                                      className="imgTeam"
                                      src={blue?.image}
                                      alt=""
                                    />
                                    <h1 className="teamName">{blue?.code}</h1>
                                  </div>
                                  <div className="team2">
                                    <img
                                      className="imgTeam"
                                      src={red?.image}
                                      alt=""
                                    />
                                    <h1 className="teamName">{red?.code}</h1>
                                  </div>
                                </div>
                                <div className="prediction">
                                  <div className="result">OVER</div>
                                  <div className="team1 ">
                                    <img
                                      className="imgTeam"
                                      src={blue?.image}
                                      alt=""
                                    />
                                    <h1 className="teamName">{blue?.code}</h1>
                                  </div>
                                  <div className="team2">
                                    <img
                                      className="imgTeam"
                                      src={red?.image}
                                      alt=""
                                    />
                                    <h1 className="teamName">{red?.code}</h1>
                                  </div>
                                </div>
                              </div>
                              <div class="nav-details">
                                <div class="nav">
                                  <div class="StatsMatchup">
                                    <FullWidthTabs
                                      cell={cell}
                                      blue={blue}
                                      red={red}
                                      bStats={bStats}
                                      rStats={rStats}
                                      bByYearStats={bByYearStats}
                                      rByYearStats={rByYearStats}
                                    />
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        </main>
                      </main>
                    </>
                  </Collapse>
                </TableRow>
              </>
            ))
          ) : (
            <SkeletonMatch />
          )}
        </TableBody>
      </Table>
    </div>
  );
}

TablePreGame.defaultProps = {
  tableHeaderColor: "gray",
};

TablePreGame.propTypes = {
  tableHeaderColor: PropTypes.oneOf([
    "warning",
    "primary",
    "danger",
    "success",
    "info",
    "rose",
    "gray",
  ]),
  tableHead: PropTypes.arrayOf(PropTypes.string),
  tableData: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),
};
