import React, { useState, useEffect, useMemo } from "react";
import { useStyles, Container, Header, Body } from "./styles";
import Layout from "../../components/Layout";
import TablePreGame from "../../components/TablePreGame";

import RadioButtonChecked from "@material-ui/icons/RadioButtonChecked";
import "../../assets/extra.css?v=1.9.0";
import "../../assets/lol.css?v=1.9.0";

import { Month, Week } from "./utils";
import api from "../../api";

const PreLive = () => {
  const classes = useStyles();
  const [matches, setMatches] = useState([]);
  var gamesList = [];
  useEffect(() => {
    async function fetchData() {
      const res = await api.get("/getSchedule?hl=pt-BR", {
        headers: {
          "x-api-key": "0TvQnueqKa5mxJntVWt0w4LpLfEkrV1Ta8rQBb9Z",
        },
      });
      console.log(res);

      var data = res.data.data.schedule.events.filter((event) => {
        return event.type === "match" && event.state !== "completed" && new Date(event.startTime).getDay() <= new Date().getDay();
      });

      return data;
    }

    fetchData().then((res) => {
      setMatches(res);
    });
  }, []);

  return (
    <div>
      <div className={classes.root}>
        <Layout />
        <main className={classes.content}>
          <div className={classes.toolbar} />
          <Container>
            <div style={{ marginBottom: "75px" }}>
              <Header>
                <h1 className={classes.cardTitleWhite}>Pré-Jogo</h1>
              </Header>
              <Body>
                <TablePreGame tableHeaderColor="primary" tableData={matches} />
              </Body>
            </div>
          </Container>
        </main>
      </div>
    </div>
  );
};

export default PreLive;
