import React from "react";
import PropTypes from "prop-types";
import SwipeableViews from "react-swipeable-views";
import { makeStyles, useTheme, withStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import WatchLater from "@material-ui/icons/WatchLater";
import Box from "@material-ui/core/Box";

const StyledTabs = withStyles({
  root: {
    background: "#030506",
    height: "100%",
    borderBottom: "1px solid #252c32",
  },
  indicator: {
    display: "flex",
    justifyContent: "center",
    backgroundColor: "#ee2737",
    "& > span": {
      maxWidth: 40,
      width: "100%",
      backgroundColor: "#ee2737",
    },
  },
})((props) => <Tabs {...props} TabIndicatorProps={{ children: <span /> }} />);

const StyledTab = withStyles((theme) => ({
  root: {
    textTransform: "uppercase",
    fontWeight: 700,
    fontSize: 20,
    color: "#fff !important",
    transition: "0.05s",
    "&:focus": {
      color: "#fff",
      opacity: 1,
    },
    "&:hover": {
      color: "#ee2737 !important",
      opacity: 0.9,
      transition: "0.1s",
    },
    MtextColorPrimary: {
      color: "white",
    },
  },
}))((props) => <Tab disableRipple {...props} />);

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  padding: {
    padding: theme.spacing(3),
  },
  demo1: {
    backgroundColor: "#030506",
  },
  demo2: {
    backgroundColor: "#2e1534",
  },
}));

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && <Box>{children}</Box>}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    "aria-controls": `full-width-tabpanel-${index}`,
  };
}

export default function FullWidthTabs({
  cell,
  blue,
  red,
  diff,
  match,
  bStats,
  rStats,
}) {
  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleChangeIndex = (index) => {
    setValue(index);
  };
  console.log(bStats);
  return (
    <div className={classes.root}>
      <AppBar position="static" color="default">
        <StyledTabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="fullWidth"
          aria-label="full width tabs example"
          centered
        >
          <StyledTab label="Estatística" {...a11yProps(0)} />
          <StyledTab label="Histórico" {...a11yProps(1)} />
        </StyledTabs>
      </AppBar>
      <div class="VodsGameSelector active-selection tab title stats selected">
        <div class="games games2">
          <div class="matchNumber">
            <span class="label">PARTIDA</span>
            <h3 class="game game1 selected watch-annotated">{cell.count}</h3>
          </div>
          <div class="matchNumber">
            <span
              class="label"
              style={{
                color: "rgb(3, 113, 156)",
              }}
            >
              {blue?.code}
            </span>
            <span class="label" style={{ color: "#ccc" }}>{bStats?.W}-{bStats?.L}</span>
          </div>
          <div class="matchNumber">
            <span class="label" style={{ color: "#ccc" }}>{rStats?.W}-{rStats?.L}</span>
            <span class="label" style={{ color: "#EE2737" }}>
              {red?.code}
            </span>
          </div>

          <div class="matchTime">
            <WatchLater
              style={{
                color: "#EE2737",
                height: "1.92857143rem",
                fontSize: "2.92857143rem",
                lineHeight: "1.92857143rem",
                marginRight: "16px",
                marginBottom: "0.1rem",
              }}
            />

            <div class="label">{diff}</div>
          </div>
        </div>
      </div>
      <SwipeableViews
        axis={theme.direction === "rtl" ? "x-reverse" : "x"}
        index={value}
        onChangeIndex={handleChangeIndex}
      >
        <TabPanel value={value} index={0} dir={theme.direction}>
          <div class="info">
            <div class="stats">
              <div class="StatsMatchupPerformance">
                <div class="player primary">
                  <div class="stat kda">
                    <div class="value">
                      <span class="kills ">
                        {typeof match !== "undefined" ? match.kills : 0}
                      </span>
                    </div>
                    <div class="title">Abates</div>
                  </div>
                  <div class="stat goldEarned">
                    <div class="value">
                      {typeof match !== "undefined" ? match.gold : 0}
                    </div>
                    <div class="title">Ouro recebido</div>
                  </div>
                  <div class="stat minionKills">
                    <div class="value">
                      {typeof match !== "undefined" ? match.cs : 0}
                    </div>
                    <div class="title">Tropas abatidas</div>
                  </div>
                  <div class="stat killParticipation">
                    <div class="value">
                      {typeof match !== "undefined" ? match.xp : 0}
                    </div>
                    <div class="title">XP Recebido</div>
                  </div>
                  <div class="stat killParticipation">
                    <div class="value">
                      {typeof match !== "undefined" ? match.barons : 0}
                    </div>
                    <div class="title">barons realizados</div>
                  </div>
                  <div class="stat wardsPlaced">
                    <div class="value">
                      {typeof match !== "undefined" ? match.dragons : 0}
                    </div>
                    <div class="title">dragões realizados</div>
                  </div>
                  <div class="stat wardsDestroyed">
                    <div class="value">
                      {typeof match !== "undefined" ? match.towers : 0}
                    </div>
                    <div class="title">Torres destruídas</div>
                  </div>
                  <div class="stat wardsDestroyed">
                    <div class="value">
                      {typeof match !== "undefined" ? match.inhibitors : 0}
                    </div>
                    <div class="title">Inibidores destruídos</div>
                  </div>
                </div>
                <div class="player secondary">
                  <div class="stat kda">
                    <div class="value">
                      <span class="kills ">
                        {typeof match !== "undefined" ? match.opp_kills : 0}
                      </span>
                    </div>
                    <div class="title">Abates</div>
                  </div>
                  <div class="stat goldEarned">
                    <div class="value">
                      {typeof match !== "undefined" ? match.opp_gold : 0}
                    </div>
                    <div class="title">Ouro recebido</div>
                  </div>
                  <div class="stat minionKills">
                    <div class="value">
                      {typeof match !== "undefined" ? match.opp_cs : 0}
                    </div>
                    <div class="title">Tropas abatidas</div>
                  </div>
                  <div class="stat killParticipation">
                    <div class="value">
                      {typeof match !== "undefined" ? match.opp_xp : 0}
                    </div>
                    <div class="title">XP Recebido</div>
                  </div>
                  <div class="stat killParticipation">
                    <div class="value">
                      {typeof match !== "undefined" ? match.opp_barons : 0}
                    </div>
                    <div class="title">barons realizados</div>
                  </div>
                  <div class="stat wardsPlaced">
                    <div class="value">
                      {typeof match !== "undefined" ? match.opp_dragons : 0}
                    </div>
                    <div class="title">dragões realizados</div>
                  </div>
                  <div class="stat wardsDestroyed">
                    <div class="value">
                      {typeof match !== "undefined" ? match.opp_towers : 0}
                    </div>
                    <div class="title">Torres destruídas</div>
                  </div>
                  <div class="stat wardsDestroyed">
                    <div class="value">
                      {typeof match !== "undefined" ? match.opp_inhibitors : 0}
                    </div>
                    <div class="title">Inibidores destruídos</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </TabPanel>
        <TabPanel value={value} index={1} dir={theme.direction}>
          <div class="info">
            <div class="stats">
              <div class="StatsMatchupPerformance">
                <div class="player primary">
                  <div class="stat kda">
                    <div class="value">
                      <span class="kills ">
                        {typeof bStats !== "undefined" ? bStats.GPR : 0}
                      </span>
                    </div>
                    <div class="title">Gold Rate</div>
                  </div>
                  <div class="stat goldEarned">
                    <div class="value">
                      {typeof bStats !== "undefined"
                        ? `${Math.round((bStats.W / bStats.GP) * 100)}%`
                        : 0}
                    </div>
                    <div class="title">Win Rate</div>
                  </div>
                  <div class="stat minionKills">
                    <div class="value">
                      {typeof bStats !== "undefined" ? bStats.MLR : 0}
                    </div>
                    <div class="title">Mid-Late Rate</div>
                  </div>
                  <div class="stat killParticipation">
                    <div class="value">
                      {typeof bStats !== "undefined" ? bStats.EGR : 0}
                    </div>
                    <div class="title">Early Game Rate</div>
                  </div>
                  <div class="stat killParticipation">
                    <div class="value">
                      {typeof bStats !== "undefined" ? `${bStats["BN%"]}` : 0}
                    </div>
                    <div class="title">Dragon Rate</div>
                  </div>
                  <div class="stat wardsPlaced">
                    <div class="value">
                      {typeof bStats !== "undefined" ? `${bStats["DRG%"]}` : 0}
                    </div>
                    <div class="title">Baron Rate</div>
                  </div>
                  <div class="stat wardsDestroyed">
                    <div class="value">
                      {typeof bStats !== "undefined" ? `${bStats["LNE%"]}` : 0}
                    </div>
                    <div class="title">Lane Control</div>
                  </div>
                  <div class="stat wardsDestroyed">
                    <div class="value">
                      {typeof bStats !== "undefined" ? bStats.KD : 0}
                    </div>
                    <div class="title">Kill/Death Ratio</div>
                  </div>
                </div>
                <div class="player secondary">
                  <div class="stat kda">
                    <div class="value">
                      <span class="kills ">
                        {typeof rStats !== "undefined" ? rStats.GPR : 0}
                      </span>
                    </div>
                    <div class="title">Gold Rate</div>
                  </div>
                  <div class="stat goldEarned">
                    <div class="value">
                      {typeof rStats !== "undefined"
                        ? `${Math.round((rStats.W / rStats.GP) * 100)}%`
                        : 0}
                    </div>
                    <div class="title">Win Rate</div>
                  </div>
                  <div class="stat minionKills">
                    <div class="value">
                      {typeof rStats !== "undefined" ? rStats.MLR : 0}
                    </div>
                    <div class="title">Mid-Late Rate</div>
                  </div>
                  <div class="stat killParticipation">
                    <div class="value">
                      {typeof rStats !== "undefined" ? rStats.EGR : 0}
                    </div>
                    <div class="title">Early Game Rate</div>
                  </div>
                  <div class="stat killParticipation">
                    <div class="value">
                      {typeof rStats !== "undefined" ? `${rStats["BN%"]}` : 0}
                    </div>
                    <div class="title">Dragon Rate</div>
                  </div>
                  <div class="stat wardsPlaced">
                    <div class="value">
                      {typeof rStats !== "undefined" ? `${rStats["DRG%"]}` : 0}
                    </div>
                    <div class="title">Baron Rate</div>
                  </div>
                  <div class="stat wardsDestroyed">
                    <div class="value">
                      {typeof rStats !== "undefined" ? `${rStats["LNE%"]}` : 0}
                    </div>
                    <div class="title">Lane Control</div>
                  </div>
                  <div class="stat wardsDestroyed">
                    <div class="value">
                      {typeof rStats !== "undefined" ? rStats.KD : 0}
                    </div>
                    <div class="title">Kill/Death Ratio</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </TabPanel>
      </SwipeableViews>
    </div>
  );
}
